/* 超小屏幕（手机，小于 768px） */
@media screen and (min-width: 0px) {
  .colorset-card {
    width: 80%;
  }
}
/* 小屏幕（平板，大于等于 768px） */
@media screen and (min-width: 868px) {
  .colorset-card {
    width: 40%;
  }
}
/* 中等屏幕（桌面显示器，大于等于 992px） */
@media screen and (min-width: 992px) {
  .colorset-card {
    width: 40%;
  }
}
/* 大屏幕（大桌面显示器，大于等于 1200px） */
@media screen and (min-width: 1200px) {
  .colorset-card {
    width: 30%;
  }
}
.colorset-card {
  display: inline-block;
  height: 250px;
  padding: 15px;
  margin: 15px 1%;
  border-width: solid 1px #ededed;
  border-radius: 5px;
  box-shadow: 0px 0px 4px 1px var(--shadowColor);
  background-color: var(--surfaceColor);
}
.colorset-card .colorset {
  display: flex;
  width: 100%;
  height: 20%;
  margin-bottom: 5%;
}
.colorset-card .colorset:hover .colorset-unit {
  flex: 1 !important;
}
.colorset-card .colorset .colorset-unit {
  flex: 1;
  border: 1px solid var(--backColor);
}
.colorset-card .colorset .colorset-unit:hover {
  flex: 6 !important;
}
.colorset-card .colorset .colorset-unit:hover .colorset-value-span {
  display: block;
}
.colorset-card .colorset .colorset-unit .colorset-value-span {
  display: none;
  background-color: rgba(0, 0, 0, 0.2);
  color: #fff;
  width: 80%;
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  cursor: pointer;
}
.colorset-card .download {
  display: flex;
  width: 100%;
  height: 20%;
  margin-top: 10px;
}
.colorset-card .download .downImg {
  flex: 1;
  height: 30px;
}
.colorset-card .download .downImg .downImg-back {
  width: 30px;
  height: 30px;
  border-radius: 15px;
  margin: auto;
}
.colorset-card .download .downImg .downImg-back:hover {
  background-color: #eee;
}
.colorset-card .download .downImg svg {
  margin-top: 5px;
}
.colorset-card .download .downJson {
  flex: 1;
  height: 30px;
}
.colorset-card .download .downJson .downJson-back {
  width: 30px;
  height: 30px;
  border-radius: 15px;
  margin: auto;
}
.colorset-card .download .downJson .downJson-back:hover {
  background-color: var(--highlightColor);
}
.colorset-card .download .downJson svg {
  margin-top: 6px;
}
.colorset-card .download .viewImg {
  flex: 1;
  height: 30px;
}
.colorset-card .download .viewImg .viewImg-back {
  width: 30px;
  height: 30px;
  border-radius: 15px;
  margin: auto;
}
.colorset-card .download .viewImg .viewImg-back:hover {
  background-color: #eee;
}
.colorset-card .download .viewImg svg {
  margin-top: 7px;
}
.colorset-card .detail {
  text-align: left;
}
.colorset-card .detail span {
  line-height: 0.78;
  font-size: 10px;
}
.colorset-unit-first {
  border-radius: 5px 0 0 5px;
}
.colorset-unit-last {
  border-radius: 0 5px 5px 0;
}
